.animate-marquee-bg::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; 
  height: 100%;
  background-color: hsl(var(--accent) / 0.3); 
  transform: translateX(-100%); 
  animation: marquee-bg-animation 1s ease-out; 
  z-index: -1; 
  pointer-events: none; 
}

